import { mapState } from "vuex";
import AppDiyForm from "../../../components/page-component/app-diy-form/app-diy-form";
import AppClose from '../../../components/basic-component/app-close/app-close.vue';

export default {
    data() {
        return {
            detail: {},
            list: {
                form: {
                    card_bg: ''
                }
            },
            card: {
                expire_day: '',
                type_info: {
                    ignore_goods: [],
                    ignore_cats: [],
                }
            },
            today: '',
            end_day: '',
            total_coupon: 0,
            total_card: 0,
            id: 0,
            end_time: '',
            start_day: '',
            success: false,
            iphone_x: false,
            template_message: [],
            loading: false,
            validateResult: {
                hasError: false
            },
            showClose: false,
            is_open: false
        }
    },
    components: {
        AppDiyForm,
        AppClose
    },
    computed: {
        ...mapState({
            svipImg: state => state.mallConfig.__wxapp_img.vip_card
        })
    },
    methods: {
        getMall(e) {
            this.is_open = e.is_open == 1 ? true : false;
            if(this.is_open) {
                this.subscribe();
            }
        },
        toDetail() {
            uni.navigateTo({
                url: '/plugins/vip_card/rights/rights?id='+ this.id
            })
        },

        toBack() {
            this.success = false;
            uni.navigateBack();
        },
        toCoupon() {
            uni.navigateTo({
                url: '/pages/coupon/index/index'
            })
        },
        toCard() {
            uni.navigateTo({
                url: '/pages/card/index/index'
            })
        },
        toBalance() {
            uni.navigateTo({
                url: '/pages/balance/balance'
            })
        },
        toIntegral() {
            uni.navigateTo({
                url: '/plugins/integral_mall/index/index'
            })
        },
        getSetting() {
            let that = this;
            that.$showLoading({
                type: 'global',
                text: '加载中...'
            });
            that.$request({
                url: that.$api.vip_card.setting,
            }).then(response=>{
                if(response.code == 0) {
                    that.list = response.data;
                    uni.setNavigationBarTitle({
                        title: '购买' + that.list.name,
                    })
                    that.template_message = response.data.template_message;
                    that.getCard();
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                that.$hideLoading();
            });
        },
        getCard() {
            let that = this;
            that.$request({
                url: that.$api.vip_card.card_detail,
                data: {
                    id: this.id
                }
            }).then(response=>{
                that.$hideLoading();
                if(response.code == 0) {
                    that.card = response.data;
                    var timestamp = Date.parse(new Date());
                    if(that.end_time > 0) {
                        timestamp = Date.parse(new Date(that.end_time));
                    }
                    // 结束
                    let endTime = (timestamp / 1000 + 24 * 60 * 60 * response.data.expire_day) * 1000;
                    let endDate = new Date(endTime)
                    let end_year = endDate.getFullYear();
                    let end_month = endDate.getMonth() + 1;
                    if (end_month >= 1 && end_month <= 9) {
                        end_month = "0" + end_month;
                    }
                    let end_now = endDate.getDate();
                    if (end_now >= 1 && end_now <= 9) {
                        end_now = "0" + end_now;
                    }
                    that.end_day = end_year + "/" + end_month + "/" + end_now;
                    that.card.coupons.forEach(v => {
                        that.total_coupon += +v.send_num
                    })
                    that.card.cards.forEach(v => {
                        that.total_card += +v.send_num
                    })
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                that.$hideLoading();
            });
        },
        subscribe() {
            if (this.validateResult.hasError) {
                uni.showToast({
                    title: this.validateResult.errors[0].msg,
                    icon: 'none'
                })
                return;
            }
            if(!this.is_open) {
                return false
            }
            this.$subscribe(this.template_message).then(res => {
                this.toSubmit();
            }).catch(res => {
                this.toSubmit();
            });
        },
        toSubmit() {
            let that = this;
            if(this.loading) {
                return false
            }
            this.loading = true;
            uni.showLoading({
                mask: true,
                title: '购买中...'
            });
            that.$request({
                url: that.$api.vip_card.order_submit,
                data: {
                    id: this.id,
                    order_form: JSON.stringify(that.list.order_form)
                },
                method: 'post'
            }).then(response => {
                if (response.code === 0) {
                    that.getPayOrderId(response.data.queue_id, response.data.token);
                } else {
                    that.loading = false;
                    uni.hideLoading();
                    uni.showModal({
                        title: '提示',
                        content: response.msg,
                        showCancel: false,
                    });
                }
            }).catch(e => {
                that.loading = false;
                uni.hideLoading();
                uni.showModal({
                    title: '提示',
                    content: e.errMsg,
                    showCancel: false,
                });
            });
        },
        handleOrderFormValidate({result}) {
            this.validateResult = result;
            this.showClose = false;
        },
        handleGoodsFormInput({data, sign}) {
            const result = [];
            for (let i in data) {
                result[i] = {
                    key: data[i].key,
                    label: data[i].name,
                    value: data[i].value,
                    required: data[i].is_required,
                };
            }
            this.list.order_form = result;
        },
        getPayOrderId(queue_id, token) {
            this.$request({
                url: this.$api.vip_card.pay_data,
                method: 'post',
                data: {
                    queue_id: queue_id,
                    token: token,
                },
            }).then(response => {
                if (response.code === 0) {
                    if (response.data.retry && response.data.retry === 1) {
                        this.getPayDataTimer = setTimeout(() => {
                            this.getPayOrderId(queue_id, token);
                        }, 1000);
                    } else {
                        this.pay(response.data.id);
                    }
                } else {
                    uni.hideLoading();
                    this.loading = false;
                    uni.showModal({
                        title: '提示',
                        content: response.msg,
                        showCancel: false,
                    });
                }
            }).catch(e => {
                this.loading = false;
                uni.hideLoading();
                uni.showModal({
                    title: '提示',
                    content: e.errMsg,
                    showCancel: false,
                });
            });
        },
        pay(id) {
            let that = this;
            that.$payment.pay(id).then(res => {
                uni.hideLoading();
                that.loading = false;
                uni.showToast({
                    title: '购买成功',
                    duration: 1000
                });
                if(that.card.send_integral_num > 0 || that.card.send_balance > 0 || that.card.coupons.length > 0 || that.card.cards.length > 0) {
                    that.success = true;
                }else {
                    setTimeout(v=>{
                        uni.navigateBack();
                    },1000)
                }
            }).catch(res => {
                uni.hideLoading();
                that.loading = false;
                uni.showToast({
                    title: '支付失败',
                    icon: 'none',
                    duration: 1000
                });
            });
        },
    },
    onUnload() {
       clearInterval(this.getPayDataTimer);
    },
    onLoad(options) { this.$commonLoad.onload(options);
        let that = this;
        uni.getSystemInfo({
            success: function (res) {
                if(res.model.indexOf('iPhone X') > -1 || res.model.indexOf('iPhone 11') > -1 || res.model.indexOf('iPhone11') > -1 || res.model.indexOf('iPhone12') > -1) {
                    that.iphone_x = true;
                }
            }
        })
        var myDate = new Date();
        let year = myDate.getFullYear();
        let month = myDate.getMonth() + 1;
        if (month >= 1 && month <= 9) {
            month = "0" + month;
        }
        let now = myDate.getDate();
        if (now >= 1 && now <= 9) {
            now = "0" + now;
        }
        this.today = year + "/" + month + "/" + now;
        this.id = options.id;
        this.end_time = options.end;
        this.expire_day = options.expire_day ? options.expire_day : 0;
        that.getSetting(options.id);
    }
}
